body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #22212c;
}
.brown.darken-1 {
  background-color: #5ca571 !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.content{
  min-height: calc(100vh - 70px - 64px);
}
.movies{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}
.search-btn{
  position: absolute;
  top: 0;
  right: 0.5rem;
}
label:not(:last-child){
  margin-right: 1rem;
}

[type="radio"]:checked + span:after,
[type="radio"].with-gap:checked + span:before,
[type="radio"].with-gap:checked + span:after {
  background-color: #7dec6b;
  border: none;
}

.list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}

.card {
  display: flex;
  flex-direction: column;
  background-color: #375a41;
}
.card .card-content .card-title {
  display: block;
  line-height: 32px;
  margin-bottom: 8px;
  font-weight: 500;
}
.card-content {
  flex-grow: 1;
}

img,
iframe {
  max-width: 100%;
}

iframe {
  width: 600px;
  height: 350px;
}
.about-container {
  display: flex;
  justify-content: center; 
  align-items: center; 
  margin-top: 1%;
  }
  
  .left-column {
  flex: 2; 
  }
  
  .right-column {
  display: flex;
  flex-direction: column; 
  flex: 1; 
  align-items: center; 
  }
  
  .povar-image {
  max-width: 100%; 
  height: auto; 
  }
  
  .povar-image-right {
  max-width: 100%; 
  height: auto; 
  width: 80%; 
  margin-bottom: 10px; 
  }
  .contact-button {
    border: none;
    border-radius: 60px;
    padding: 10px 20px;
    background-color: #00bfff; 
    color: white;
    cursor: pointer;
    }
    
   
    .da{
      font-size: 34px;
      margin-top: 1.2%;
      color: aliceblue;
    
    }
.container{
  width: 100%;
}
.contact-p{
  font-size: 30px;
  margin-bottom: 1%;
}
.phone-btn{
  font-size: 40px;
  font-size: 40px;
  border: none;
    border-radius: 60px;
    padding: 1px 20px;
    background-color:  #0e0e16;
    color: antiquewhite;
    cursor: pointer;
}
.phone-btn:hover {
  background-color: #5ca571;  /* Цвет при наведении */
  }
.address-btn{
  font-size: 40px;
  border: none;
    border-radius: 60px;
    padding: 1px 20px;
    background-color: #0e0e16;
    color: antiquewhite;
    cursor: pointer;
}
.address-btn:hover {
  background-color: #5ca571;  /* Цвет при наведении */
  }
button, input, optgroup, select, textarea {
  font-family: sans-serif;
  font-size: 100%;
  line-height: 2;
  padding-left: 1%;
  padding-right: 1%;
}

  
  .button-block {
  align-self: flex-start;
  margin-bottom: 20px;
  }
  
  .content-column {
  flex: 1;
  display: flex;
  justify-content: center;
  }
  
  
  .contact-info {
  display: flex;
  align-items: center;
  margin-left: 5%;
  color: antiquewhite;
  }
  
  .contact-label {
  margin-right: 10px;
  font-size: 20px;
  margin-top: 14px;
  color: antiquewhite;
  }
  
  .phone-btn,
  .address-btn {
  border: none;
  font-size: 20px;
  color: antiquewhite;
  cursor: pointer;
  }
  
  .map {
  margin-top: 1%;
  margin-bottom: 1%;
  display: flex;
  justify-content: center;
  }
  
  .karta{
  width: 50%;
  align-items: center;
  }
  .btn, .btn-large, .btn-small {
    text-decoration: none;
    color: #fff;
    background-color:  #0e0e16;
    text-align: center;
    letter-spacing: .5px;
    -webkit-transition: background-color .2s ease-out;
    transition: background-color .2s ease-out;
    cursor: pointer;
}
.btn:hover, .btn-large:hover, .btn-small:hover {
  background-color: #5ca571; 
  }
  .h1, h6, h5, p, table{
    color: aliceblue;
  }
 